import { getObjectAtPath, setObjectAtPath, isObjectLiteral } from './util';
import Color from 'color';

export const tracers = {}
export const tracersById = {};
export const tracersByPath = {};
export const colorsByTracerId = {};
export const events = [];
export const ids = {}
export const regions = {};
const headerNodesByTitle = {};

const COLORS = {
  BACKGROUND: '#666',
  HEADER: {
    LINE: '#ccc'
  },
  BODY: {
    BACKGROUND: '#666',
    LINE: '#aaa'
  }
}

let eventY = 0;
const HEADER_ROW_HEIGHT = 40;
const BODY_ROW_HEIGHT = 30;
const virtualElements = new Map();
const CSS_COLOR_NAMES = ["AliceBlue","AntiqueWhite","Aqua","Aquamarine","Azure","Beige","Bisque","Black","BlanchedAlmond","Blue","BlueViolet","Brown","BurlyWood","CadetBlue","Chartreuse","Chocolate","Coral","CornflowerBlue","Cornsilk","Crimson","Cyan","DarkBlue","DarkCyan","DarkGoldenRod","DarkGray","DarkGrey","DarkGreen","DarkKhaki","DarkMagenta","DarkOliveGreen","Darkorange","DarkOrchid","DarkRed","DarkSalmon","DarkSeaGreen","DarkSlateBlue","DarkSlateGray","DarkSlateGrey","DarkTurquoise","DarkViolet","DeepPink","DeepSkyBlue","DimGray","DimGrey","DodgerBlue","FireBrick","FloralWhite","ForestGreen","Fuchsia","Gainsboro","GhostWhite","Gold","GoldenRod","Gray","Grey","Green","GreenYellow","HoneyDew","HotPink","IndianRed","Indigo","Ivory","Khaki","Lavender","LavenderBlush","LawnGreen","LemonChiffon","LightBlue","LightCoral","LightCyan","LightGoldenRodYellow","LightGray","LightGrey","LightGreen","LightPink","LightSalmon","LightSeaGreen","LightSkyBlue","LightSlateGray","LightSlateGrey","LightSteelBlue","LightYellow","Lime","LimeGreen","Linen","Magenta","Maroon","MediumAquaMarine","MediumBlue","MediumOrchid","MediumPurple","MediumSeaGreen","MediumSlateBlue","MediumSpringGreen","MediumTurquoise","MediumVioletRed","MidnightBlue","MintCream","MistyRose","Moccasin","NavajoWhite","Navy","OldLace","Olive","OliveDrab","Orange","OrangeRed","Orchid","PaleGoldenRod","PaleGreen","PaleTurquoise","PaleVioletRed","PapayaWhip","PeachPuff","Peru","Pink","Plum","PowderBlue","Purple","Red","RosyBrown","RoyalBlue","SaddleBrown","Salmon","SandyBrown","SeaGreen","SeaShell","Sienna","Silver","SkyBlue","SlateBlue","SlateGray","SlateGrey","Snow","SpringGreen","SteelBlue","Tan","Teal","Thistle","Tomato","Turquoise","Violet","Wheat","White","WhiteSmoke","Yellow","YellowGreen"];
let colorIndex = 0;

// https://stackoverflow.com/questions/13382516/getting-scroll-bar-width-using-javascript
export function getScrollbarWidth() {
  var outer = document.createElement("div");
  outer.style.visibility = "hidden";
  outer.style.width = "100px";
  outer.style.msOverflowStyle = "scrollbar"; // needed for WinJS apps

  document.body.appendChild(outer);

  var widthNoScroll = outer.offsetWidth;
  // force scrollbars
  outer.style.overflow = "scroll";

  // add innerdiv
  var inner = document.createElement("div");
  inner.style.width = "100%";
  outer.appendChild(inner);

  var widthWithScroll = inner.offsetWidth;

  // remove divs
  outer.parentNode.removeChild(outer);

  return widthNoScroll - widthWithScroll;
}

window.getScrollbarWidth = getScrollbarWidth;

export function nextId (id) {
  if (!ids.hasOwnProperty(id)) {
    return ids[id] = 0;
  }
  const value = ids[id] + 1;
  ids[id] = value;
  return value;
}

const state = {
  currentId: null
};

export function ensureTracer (id) {
  const tracerId = tracersById[id];

  if (tracerId) {
    // return current id if already tracked
    return tracerId;
  }

  // NEW: get array of path parts
  let paths = id.split('.');
  if (paths.length > 0 && !paths[0]) {
    // relative path, prepend the current id
    id = `${state.currentId}${id}`;
    paths = id.split('.');
  } else {
    // absolute path
    state.currentId = id;
  }

  // build an array which traverses the path tree
  const tree = [];

  paths.forEach((path, i) => {
    const subPath = paths.slice(0, i + 1).join('.');
    tree.push(subPath);
  });

  tree.forEach(path => {
    let hasValue = true;
    let value = undefined;
    try {
      value = getObjectAtPath(tracers, path, true);
    } catch (e) {
      hasValue = false;
    }
    if (!hasValue) {
      const id = nextId('tracersById');
      tracersByPath[path] = id;
      tracersById[id] = path;
      setObjectAtPath(tracers, path, {});
    }
  });

  return tree[tree.length -1];
}

export function getParams (args) {
  let params = {};
  const firstArg = args[0];
  if (firstArg && args.length === 1 && firstArg.hasOwnProperty('length')) {
    const l = firstArg.length;
    for (let i = 0; i < l; i++) {
      params[i] = firstArg[i];
    }
  } else if (args.length === 1 && isObjectLiteral(firstArg)) {
    for (let key in firstArg) {
      params[key] = firstArg[key];
    }
  } else {
    const l = args.length;
    for (let i = 0; i < l; i++) {
      params[i] = args[i];
    }
  }
  return params;
}

export function trace (id, ...args) {
  const tracerPath = ensureTracer(id);
  const tracerId = tracersByPath[tracerPath];
  const params = getParams(args);
  events.push({
    path: tracerPath,
    id: id,
    tracerId: tracerId,
    params: params,
    y: eventY
  });
  eventY += BODY_ROW_HEIGHT;
}

function getVirtualElement (element, totalWidth, hasOverflow) {
  const renderContext = element.renderContext;

  if (!virtualElements.has(element)) {
    // first time render, initialise dom
    element.classList.add('trace');
    const virtual = document.createElement('div');
    virtual.classList.add('virtual');
    virtual.style.width = `${totalWidth}px`;

    const container = document.createElement('div');
    container.classList.add('virtual-container');
    container.appendChild(virtual);
    if (!hasOverflow) {
      container.style.overflowX = 'hidden';
    }

    let canvas;

    container.addEventListener('scroll', e => {
      if (!canvas) {
        canvas = element.querySelector('canvas');
      }

      const scrollTop = Math.min(renderContext.maxScrollTop, container.scrollTop);
      const scrollLeft = Math.min(renderContext.totalWidth, container.scrollLeft);
      renderContext.scrollTop = scrollTop;
      renderContext.scrollLeft = scrollLeft;

      canvas.style.left = `${-scrollLeft}px`;

      renderContent(renderContext);
    });

    element.appendChild(container);
    virtualElements.set(element, virtual);
  }
  return virtualElements.get(element);
}

export function render (selectorOrElement, totalWidth) {
  const element = typeof selectorOrElement === 'string' ? document.querySelector(selectorOrElement) : selectorOrElement;
  if (element.renderContext) {
    return renderContent(element.renderContext);
  }

  totalWidth = typeof totalWidth === 'number' ? totalWidth : element.clientWidth;
  const hasOverflow = element.clientWidth !== totalWidth;
  const totalHeight = element.clientHeight;
  const canvas = document.createElement('canvas');
  const ctx = canvas.getContext('2d');
  canvas.width = totalWidth;
  canvas.height = totalHeight;

  // new render context
  const renderContext = {
    element: element,
    canvas: canvas,
    ctx: ctx,
    headerNodes: [],
    totalWidth: totalWidth,
    totalHeight: totalHeight,
    headerHeight: 0,
    bodyHeight: 0,
    tracerCoords: {},
    rendered: {
      header: new Map(),
      body: new Map()
    },
    maxScrollTop: 0,
    mouse: {
      x: 0,
      y: 0
    },
    scrollTop: 0,
    scrollLeft: 0,
    focus: null,
    selected: null,
    hover: null
  };

  element.renderContext = renderContext;

  calcHeader(renderContext);
  renderContent(renderContext);

  element.appendChild(canvas);

  const virtualElement = getVirtualElement(element, totalWidth, hasOverflow);
  virtualElement.style.top = `${renderContext.headerHeight}px`;
  virtualElement.style.width = `${totalWidth}px`;
  virtualElement.style.height = `${renderContext.bodyHeight}px`;

  renderContext.maxScrollTop = renderContext.bodyHeight - renderContext.headerHeight - (BODY_ROW_HEIGHT * 2);

  const mouseCoordsHandler = e => {
    const elementOffsetX = element.getBoundingClientRect().left;
    const elementOffsetY = element.getBoundingClientRect().top;
    const x = e.pageX - elementOffsetX + element.scrollLeft;
    const y = e.pageY - elementOffsetY + element.scrollTop;
    renderContext.mouse.x = x;
    renderContext.mouse.y = y;
    const object = getObjectAt(e, renderContext);
    renderContent(renderContext);
    renderContext.hover = object;
    if (focus) {
      focus(renderContext, object);
    }
  };

  element.addEventListener('mouseover', mouseCoordsHandler);
  element.addEventListener('mouseout', mouseCoordsHandler);
  element.addEventListener('mousemove', mouseCoordsHandler);
  element.addEventListener('click', e => {
    const object = getObjectAt(e, renderContext);
    console.log(object);
  });
}

function focus (renderContext, object) {
  if (!object) {
    return;
  }
  const isHeaderObject = object.type === 'header';
  
  if (!isHeaderObject) {
    const color = colorsByTracerId[object.id];
   
  }
}

function getPoint (e, renderContext) {
  const { mouse } = renderContext;
  return {
    x: mouse.x + renderContext.scrollLeft,
    y: mouse.y + renderContext.scrollTop,
    absX: mouse.x,
    absY: mouse.y
  };
}

function getObjectAt (e, renderContext) {
  const { headerHeight, rendered: { body: bodyRenders, header: headerRenders } } = renderContext;
  const mouse = getPoint(e, renderContext);
  const inHeader = mouse.absY < headerHeight;
  const objects = inHeader ? headerRenders : bodyRenders;
  const mouseY = mouse.y;
  if (inHeader) {
    mouse.y = mouse.absY;
  }
  for (let rect of objects.keys()) {
    // if rect of object
    if ((mouse.x >= rect.x && mouse.x <= rect.x + rect.width) && (mouse.y >= rect.y && mouse.y <= rect.y + rect.height)) {
      return objects.get(rect);
    }
  }
  if (inHeader) {
    // try body
    mouse.y = mouseY;
    for (let rect of bodyRenders.keys()) {
      if ((mouse.x >= rect.x && mouse.x <= rect.x + rect.width) && (mouse.y >= rect.y && mouse.y <= rect.y + rect.height)) {
        return bodyRenders.get(rect);
      }
    }
  }
  return null;
}

function renderShadow (ctx, x, y, w, h, opacity = 0.7) {
  const gradient = ctx.createLinearGradient(x, y, x, y + h);
  gradient.addColorStop(0, `rgba(0,0,0,${opacity})`);
  gradient.addColorStop(1, 'rgba(0,0,0,0)');
  ctx.fillStyle = gradient;
  ctx.fillRect(x, y, w, h);
}

function calcHeader (renderContext) {
  const { header, ctx, totalWidth, tracerCoords } = renderContext;
  calcHeaderNode(null, renderContext, tracers, renderContext.totalWidth);
}

function calcHeaderNode (parentNode, renderContext, node, availableWidth, level = 0, title = null, x = 0, y = 0) {
  const headerNode = {
    type: 'header',
    parent: parentNode,
    title,
    availableWidth,
    x,
    y,
    color: null,
    level: level
  };

  headerNodesByTitle[title] = headerNode;

  renderContext.headerNodes.push(headerNode);

  if (title) {
    const parts = title.split('.');
    const parentPath = parts.slice();
    parentPath.splice(parentPath.length - 1, 1);
    const nodeTitle = parts[parts.length - 1];

    headerNode.parentPath = parentPath;
    headerNode.nodeTitle = nodeTitle;
  }

  // collect children
  const children = [];
  for (let key in node) {
    children.push({
      parent: headerNode,
      key: key,
      value: node[key]
    });
  }

  if (children.length === 0) {
    headerNode.doesTerminate = true;
  }

  if (title === null) {
    headerNode.isRoot = true;
  }

  // find x, y for each child
  const subWidth = availableWidth / children.length;
  headerNode.subWidth = subWidth;
  let sx = x;
  let sy = y + HEADER_ROW_HEIGHT;
  renderContext.headerHeight = Math.max(sy, renderContext.headerHeight);

  for (let i = 0; i < children.length; i++) {
    const child = children[i];
    child.x = sx;

    // assign color
    const midTone = 50;
    const midRemainder = 255 - midTone;
    let color = Color.rgb(midTone + Math.random() * midRemainder, midTone + Math.random() * midRemainder, midTone + Math.random() * midRemainder);
    color = color.lighten(0.1);
    colorIndex = colorIndex % CSS_COLOR_NAMES.length;
    colorsByTracerId[title ? title + '.' + child.key : child.key] = color;

    // track x for tracer id
    renderContext.tracerCoords[title ? title + '.' + child.key : child.key] = {
      x: sx,
      width: subWidth
    };

    sx += subWidth;
  }

  // calc children
  const childHeaderNodes = [];

  for (let i = 0; i < children.length; i++) {
    const child = children[i];
    const childHeaderNode = calcHeaderNode(
      headerNode,
      renderContext,
      child.value,
      subWidth,
      level + 1,
      title ? title + '.' + child.key : child.key,
      child.x,
      title ? sy : 0
    );
    childHeaderNodes.push(childHeaderNode);
  }

  headerNode.children = childHeaderNodes;

  return headerNode;
}

function getHeaderNodeDepth (headerNode) {
  const { level } = headerNode;
  let maxLevel = 0;
  const walk = node => {
    maxLevel = Math.max(maxLevel, node.level);
    node.children && node.children.forEach(child => walk(child));
  }
  walk(headerNode);
  return level / maxLevel;
}

function getColor (title) {
  const headerNode = headerNodesByTitle[title];
  let color = colorsByTracerId[title];
  if (!headerNode.isRoot) {
    const rootTitle = title.split('.')[0];
    color = colorsByTracerId[rootTitle].darken(getHeaderNodeDepth(headerNode) * 0.5);
  }
  return color;
}

function renderContent (renderContext) {
  const { ctx, totalWidth, totalHeight } = renderContext;
  renderContext.rendered.header.clear();
  renderContext.rendered.body.clear();

  ctx.fillStyle = COLORS.BACKGROUND;
  ctx.fillRect(0, 0, totalWidth, totalHeight);

  window.rx = renderContext;

  renderBody(renderContext);
  renderHeader(renderContext);
}

function renderHeader (renderContext) {
  const { ctx, totalWidth, headerHeight, headerNodes, rendered: { header: headerRenders }, hover } = renderContext;

  for (let i = 0; i < headerNodes.length; i++) {
    const headerNode = headerNodes[i];
    const { title, availableWidth, nodeTitle, x, y } = headerNode;

    if (title) {
      const rect = {
        x,
        y,
        width: availableWidth,
        height: HEADER_ROW_HEIGHT
      };
      headerNode.rect = rect;
      headerRenders.set(rect, headerNode);

      // render appearance
      let color = getColor(headerNode.title);
      color = hover === headerNode ? color.lighten(0.2) : color;
      ctx.fillStyle = color;
      ctx.fillRect(x, y, availableWidth, HEADER_ROW_HEIGHT);
      ctx.strokeStyle = COLORS.HEADER.LINE;
      ctx.strokeRect(x, y, availableWidth, HEADER_ROW_HEIGHT);

      // render title
      ctx.font = "16px courier";
      ctx.textAlign = "center";
      ctx.fillStyle = color.mix(Color('black'), 0.5);
      ctx.fillText(nodeTitle, (x + availableWidth / 2) + 1, (y + HEADER_ROW_HEIGHT / 2 + 3) + 1, availableWidth);
      ctx.fillStyle = color.mix(Color('white'), 0.75);
      ctx.fillText(nodeTitle, x + availableWidth / 2, y + HEADER_ROW_HEIGHT / 2 + 3, availableWidth);
    }

    if (headerNode.doesTerminate) {
      renderShadow(ctx, x, y + HEADER_ROW_HEIGHT, availableWidth, BODY_ROW_HEIGHT / 3, 0.3);
      ctx.fillStyle = 'white';
      ctx.fillRect(x, y + HEADER_ROW_HEIGHT, availableWidth, 1);
    }
  }
}

function getVisibleRange (renderContext) {
  const { totalHeight, headerHeight, bodyHeight, scrollTop } = renderContext;
  const visibleHeight = (totalHeight - headerHeight);
  const headerRows = Math.floor(headerHeight / BODY_ROW_HEIGHT);
  const scrolledCount = scrollTop / BODY_ROW_HEIGHT;
  const top = Math.max(0, Math.ceil(scrolledCount) - 1);
  const range = Math.ceil((totalHeight - headerHeight - getScrollbarWidth()) / BODY_ROW_HEIGHT);
  const bottom = Math.min(top + range, events.length - 1);
  const headerTop = Math.max(top - headerRows, 0);
  return {top, bottom, headerTop, headerRows};
}

function renderBody (renderContext) {
  const { headerHeight, totalHeight } = renderContext;
  const availableHeight = totalHeight - headerHeight;

  const { top, bottom, headerTop, headerRows } = getVisibleRange(renderContext);

  if (top >= 1) {
    for (let i = headerTop; i < top; i++) {
      renderBodyRow(renderContext, events[i]);
    }
  }

  for (let i = top; i <= bottom; i++) {
    renderBodyRow(renderContext, events[i]);
  }

  renderContext.bodyHeight = events.length * BODY_ROW_HEIGHT;
}

function renderBodyRow (renderContext, event) {
  const { headerHeight, ctx, totalWidth, tracerCoords, rendered: { body: bodyRenders }, hover } = renderContext;
  const { id, params, path, tracerId, x, y: ey } = event;
  const coords = tracerCoords[path];
  const y = ey + headerHeight - renderContext.scrollTop;

  // store geom for body item rendered
  const rect = {
    x: 0,
    y: y + renderContext.scrollTop,
    width: totalWidth,
    height: BODY_ROW_HEIGHT
  };
  event.rect = rect;
  bodyRenders.set(rect, event);

  // render appearance
  let color = getColor(tracersById[tracerId]);
  // ctx.fillStyle = color.darken(0.2);
  // ctx.fillStyle = 'black';
  ctx.fillStyle = hover === event ? color.mix(Color('black'), 0.6) : color.mix(Color('black'), 0.7);
  ctx.fillRect(0, y, totalWidth, BODY_ROW_HEIGHT);
  ctx.fillStyle = color;
  ctx.fillRect(coords.x, y, coords.width, BODY_ROW_HEIGHT);
  ctx.strokeStyle = '#777';
  ctx.strokeRect(0, y + BODY_ROW_HEIGHT, totalWidth, y + BODY_ROW_HEIGHT);

  // render text
  ctx.font = '12px courier';
  ctx.textAlign = 'left';
  ctx.fillStyle = color.mix(Color('white'), 0.5);
  // ctx.fillText(id, coords.x, y + (BODY_ROW_HEIGHT / 2) + 3, coords.width);
  let args;
  try {
    args = JSON.stringify(event.params);
  } catch (e) {
    args = '' + args;
  }
  ctx.fillText(args, 0, y + (BODY_ROW_HEIGHT / 2) + 3);
}

export default trace;


